body{background-color:black;color:white;font-family:'Vollkorn', serif}main{width:100%;height:100%;display:flex;flex-direction:column;flex-wrap:nowrap}ul{text-align:center;list-style:none;display:inline-block}li{cursor:pointer;margin:10px 0;font-size:20px}.modal{display:none;position:fixed;z-index:1;left:0;top:0;width:100%;height:100%;overflow:auto;background-color:#000;background-color:rgba(0,0,0,0.797)}#modalText{z-index:3;text-align:center;width:90%;position:fixed;top:45%;left:50%;transform:translate(-50%, -50%);font-size:10vh;line-height:10vh}#wordModal{display:none;position:fixed;top:5%;padding:15px;margin:10px;text-align:center;z-index:3;width:100%;border-color:red;cursor:pointer}#wordModal>#defH3,#definitions,#etymology{display:inherit}#definitions>li{display:inherit}#authorDiv{cursor:pointer;font-size:20px;display:inline-block}#authorDiv:hover{text-decoration:line-through}#buttonStyle{display:flex;font-size:15px;font-family:inherit;background-color:transparent;border:.5px solid white;padding:10px;cursor:pointer;overflow:hidden;color:white;margin:20px}footer{position:fixed;display:flex;justify-content:space-between;width:100%;bottom:0;background-color:black;border-top:1px solid #4d3d16}#appInfo{font-family:'UnifrakturMaguntia', cursive;font-size:5vh;text-align:right;float:right;vertical-align:top;padding-right:.5em}#appInfo:hover{-webkit-text-decoration:white line-through;text-decoration:white line-through}#line{width:50%}header{position:fixed;display:inline-block;width:100%;top:0;height:40px;padding-bottom:1.8em;background-color:black;border-bottom:1px solid #4d3d16;z-index:1;text-align:center}#authorInfo{border:1px solid #2b2a28;overflow:auto;margin:0px;float:left;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin-left:15px;margin-top:1em;max-height:70em;opacity:0;padding:.5em;padding-top:0px;transition:opacity 3s;position:fixed;margin-top:4.5em;width:22.5%}.flexBox{flex-shrink:0}#leftArrow{position:fixed;padding:15px;font-size:80px;line-height:16em;color:white;height:auto}#rightArrow{height:-webkit-max-content;height:-moz-max-content;height:max-content;position:fixed;width:1.5em;right:0px;font-size:80px;line-height:16em;color:white;height:auto}#rightArrow:active,#leftArrow:active{text-shadow:2px 2px rgba(255,255,255,0.673);color:black}span:hover{text-shadow:2px 2px rgba(255,255,255,0.673);color:darkgoldenrod}#content{display:inline-flex;justify-content:center;margin-top:4em;width:100%}#poemDiv{display:inline-block;text-align:center;flex-grow:1;overflow:auto;min-height:2em;border-bottom:1px solid #282828;padding-bottom:5em}#appDiv{width:22.5%;border:1px solid #2b2a28;float:right;overflow:auto;margin:0px;margin-right:15px;margin-top:1em;max-height:70em;opacity:0;padding:.5em;padding-top:0px;transition:opacity 3s;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;position:fixed;right:0;margin-top:4.5em}#poemH{display:inline-block}#copy{align-self:flex-end;color:#5a5a5a;font-size:14px}.fa-github{font-family:'Font Awesome 5 Brands';margin-right:20px;margin-bottom:15px;font-size:2em;color:#5a5a5a;align-self:flex-end}.fa-github:hover{color:white}.fa-align-justify{margin-left:5px}#divBlock{padding-left:1em;margin-top:1em;vertical-align:top;float:left}li:hover{color:darkgoldenrod}
